.main-nav {
  display: flex;
  @include r(820) {
    flex-direction: column; }
  &-i {
    display: flex;
    width: 100%;
    @include r(820) {
      flex-direction: column;
      width: 100%;
      margin-top: 100px; } }
  @include r(820) {
    display: none; }
  &.is-active {
    display: flex; }

  &__item {
    min-width: 65px;
    margin-right: 30px;
    @include r(820) {
      width: 100%;
      margin: 0;
      text-align: center;
      border-bottom: 1px solid $green;
      background-color: lightgreen;
      &:hover {
        background-color: $green; }
      a {
        display: block;
        width: 100%;
        padding: 10px;
        &:hover {
          color: white;
          border-bottom: none; } } }
    a {
      @include pta;
      color: $darck;
      @include css-lock($min-size: 12, $max-size: 14, $min-width: 820, $max-width: 1600, $property: font-size, $unit: px);
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.5px;
      &:hover {
        color: $green;
        border-bottom: 2px solid $green; } } }

  &--left {
    margin-left: auto;
    padding-right: 190px;
    @include r(820) {
      padding: 0;
      margin: 0; } }
  &--right {
    margin-right: auto;
    @include r(820) {
      padding: 0;
      margin: 0; } } }
