.page-discription {
  display: flex;
  width: 50%;
  min-height: 850px;
  z-index: 1;
  padding-top: 250px;
  @include css-lock($min-size: 40, $max-size: 190, $min-width: 820, $max-width: 1600, $property: padding-left, $unit: px);
  padding-right: 50px;
  background: url(../img/slider-bg.png) no-repeat 180% 95%, url(../img/discr-bg-2.png) no-repeat 100% 50%;
  @include r(1366) {
    min-height: 725px;
    padding-top: 80px; }
  @include r(1025) {
    min-height: 450px; }
  @include r(820) {
    width: 100%;
    min-height: 200px;
    padding-top: 40px; }
  &-i {
    margin-left: auto;
    @include r(820) {
      width: 100%; } }

  &__text {
    max-width: 555px;
    margin-bottom: 40px;
    p {
      color: $grey;
      @include r(1025) {
        font-size: 16px; }
      @include r(410) {
        font-size: 12px;
        margin-bottom: 20px; } } } }
