.breadcrumbs {
  display: flex;
  margin-bottom: 15px;
  &__item {
    position: relative;
    margin-right: 20px;
    &:after {
      content: "";
      position: absolute;
      top: 8px;
      right: -15px;
      width: 10px;
      height: 10px;
      background: url(../img/right-arrow.png) no-repeat 100% 40%; }
    a {
      @include pta;
      color: #f7f7f7;
      opacity: 0.4;
      font-size: 16px;
      @include r(340) {
        font-size: 14px; }
      &:hover {
        color: $green;
        opacity: 1; } } } }
