$grey: #f7f7f7;
$darck: #353744;
$green: #63b250;

@include font(pta, 'Ptastrasans');
@include font(sfui, 'sfuitext-heavy');

@mixin pta {
  font-family: pta, sans-serif;
  font-size: 20px;
  line-height: 1.25;
  font-weight: 400;
  color: $darck; }

@mixin sfui {
  font-family: sfui , sans-serif;
  font-size: 42px;
  line-height: 1.2;
  font-weight: 800;
  text-transform: uppercase;
  color: $grey;
  letter-spacing: 0.5px;
  @include r(1085) {
    font-size: 32px; }
  @include r(410) {
    font-size: 24px; } }

body {
  @include pta; }
a {
  text-decoration: none; }
