.hero {
  display: flex;
  width: calc(100% + 60px);
  min-height: 850px;
  margin: 0 auto;
  margin-bottom: 51px;
  background-color: $darck;
  @include r(1366) {
    min-height: 725px; }
  @include r(1025) {
    min-height: 450px; }
  @include r(820) {
    flex-direction: column;
    width: 100%;
    min-height: 400px; } }

