.promo {
  background-color: #353744;
  padding-bottom: 60px;
  @include r(1300) {
    padding-bottom: 60px; }
  @include r(1024) {
    padding-bottom: 40px; }
  @include r(600) {
    padding-bottom: 10px; }

  &__list {
    display: flex;
    @include r(600) {
      flex-direction: column; } }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 34%;
    padding-top: 27px;
    @include r(600) {
      width: 100%;
      border-bottom: 1px solid $grey;
      padding-bottom: 20px;
      &:last-child {
        border-bottom: none; } }

    &:after {
      position: absolute;
      top: 0;
      right: -5px;
      content: "";
      width: 10px;
      height: 250px;
      background: url('../img/promo-border.png') no-repeat 100% -20%;
      @include r(600) {
        display: none; } }
    &:last-child:after {
      display: none; } }

  &__pic {
    width: calc(100% - 160px);
    margin-top: auto;
    margin-bottom: 20px;
    @include r(1300) {
      width: 80%; } }

  &__title {
    display: flex;
    width: 140px;
    min-height: 34px;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase; } }
