.header {
  position: relative;
  width: 100%;
  z-index: 4;
  background-color: #fff;
  box-shadow: 0 0 0 10px rgba(53, 55, 68, 0.2);
  @include r(820) {
    & .container {
      padding: 0; } }
  &-i {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 105px;
    padding: 0 80px;
    background-color: #fff;
    &:before {
      content: "";
      position: absolute;
      top: 50px;
      left: 500px;
      z-index: -1;
      width: 205px;
      height: 90px;
      background: white;
      border-radius: 120px / 50px;
      box-shadow: 0 0 0 10px rgba(53, 55, 68, 0.2);
      @include r(1250) {
        left: 50%;
        transform: translate(-50%); } }
    &:after {}
    @include r(820) {
      padding: 0; } } }
