.accordion {
  &__item {
    border-bottom: 2px solid #dadadc;
    cursor: pointer;
    &.is-active a {
      &:before {
        transform: rotate(90deg);
        top: 25px; }
      a {
        color: $green; } } }

  &__trigger {
    position: relative;
    display: block;
    padding: 18px 40px;
    @include pta;
    font-size: 30px;
    @include css-lock($min-size: 24, $max-size: 30, $min-width: 820, $max-width: 1600, $property: font-size, $unit: px);
    line-height: 1;
    font-weight: 700;
    letter-spacing: 1px;
    color: #353744;
    &:before {
      position: absolute;
      top: 35%;
      left: 15px;
      content: "";
      width: 0;
      height: 0;
      border-top: 9px solid transparent;
      border-left: 12px solid $green;
      border-bottom: 9px solid transparent; }
    @include r(800) {
      font-size: 20px; }
    @include r(400) {
      font-size: 16px; }
    &:hover {
      color: $green; } }

  &__descr {
    display: none;
    margin-left: 40px;
    img {
      width: 640px;
      height: 480px;
      margin-top: 15px;
      margin-bottom: 16px;
      object-fit: cover;
      object-position: 50% 50%;
      @include r(800) {
        width: 100%;
        height: auto; } }
    p {
      max-width: 1040px;
      margin-bottom: 45px;
      @include r(800) {
        font-size: 16px; }
      @include r(400) {
        font-size: 12px; } } } }
