.lang {
  position: absolute;
  top: 40px;
  right: 0;
  display: flex;
  @include r(820) {
    top: 35px;
    right: 20px; }
  &__item {
    display: flex;
    width: 40px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
    a {
      margin: auto;
      margin-bottom: 8px;
      @include pta;
      color: $darck;
      font-size: 14px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.5px;
      &:hover {
        color: $green; } }
    &.is-active {
      background: url(../img/lang-bg.png) no-repeat;
      a {
        color: #f1f5f8; } } } }
