.tabs {
  margin-bottom: 43px;
  text-align: center;
  @include r(500) {
    margin-bottom: 20px; }
  &__item {
    position: relative;
    padding: 7px 33px;
    background-color: $darck;
    @include pta;
    font-size: 25px;
    color: white;
    @include r(820) {
      font-size: 20px; }
    @include r(400) {
      font-size: 14px; }

    &:before {
      position: absolute;
      top: 0;
      left: 0;
      content: "";
      width: 0;
      height: 0;
      border-top: 47px solid white;
      border-right: 20px solid transparent;
      border-bottom: 0 solid transparent; }
    &:after {
      position: absolute;
      top: 0;
      right: 0;
      content: "";
      width: 0;
      height: 0;
      border-top: 0 solid transparent;
      border-left: 20px solid transparent;
      border-bottom: 47px solid white; }
    &:hover {
      background-color: $green; }
    &.is-active {
      background-color: $green;
      @include r(500) {
        display: flex;
        width: 50%;
        margin: 0 auto;
        margin-top: 10px; } }
    &.is-active {
      cursor: default; } } }
