.slider {
  position: relative;
  width: calc(50% + 60px);
  height: 850px;
  margin-left: -30px;
  @include r(1366) {
    height: 725px; }
  @include r(1025) {
    height: 450px; }
  @include r(820) {
    width: 100%;
    height: 400px;
    margin-left: 0; }
  @include r(410) {
    height: 300px; }

  &__item {
    height: 850px;
    @include r(1366) {
      height: 725px; }
    @include r(1025) {
      height: 450px; }
    @include r(820) {
      height: 400px; }
    @include r(410) {
      height: 300px; }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 50%; } }

  &__arrow {
    display: flex;
    position: absolute;
    top: calc(50% + 3px);
    transform: translateY(-50%);
    width: 50px;
    height: 50px;
    opacity: 0.4;
    background-color: black;
    border-radius: 50%;
    cursor: pointer;
    &:hover {
      opacity: 1; }
    &--prev {
      left: 15px; }
    &--next {
      right: 75px;
      @include r(820) {
        right: 15px; } }
    &--prev:after, &--next:after {
      margin: auto;
      z-index: 5;
      content: "";
      width: 0;
      height: 0;
      border-top: 10px solid transparent;
      border-bottom: 10px solid transparent; }
    &--prev:after {
      border-right: 15px solid white; }
    &--next:after {
      border-left: 15px solid white; } } }

.slick-slide {
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 50%; } }

.my-dots {
  position: absolute;
  left: 45%;
  transform: translate(-50%);
  bottom: 45px;
  display: block;
  width: 100%;
  padding: 0;
  list-style: none;
  text-align: center;
  li {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;
    button {
      font-size: 0;
      line-height: 0;
      display: block;
      width: 20px;
      height: 20px;
      padding: 5px;
      cursor: pointer;
      color: transparent;
      border: 0;
      outline: none;
      background: transparent;
      &:hover, &:focus {
        outline: none; }
      &:hover:before, &:focus:before {
        opacity: 1; }
      &:before {
        font-family: 'slick';
        font-size: 6px;
        line-height: 20px;
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        content: url('../img/slider-dot.svg');
        text-align: center;
        opacity: .6;
        color: black;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale; } }
    &.slick-active button:before {
      opacity: 1;
      color: black; } } }
