.out {
  overflow: hidden; }
.logo {
  position: absolute;
  top: 25px;
  left: 50%;
  z-index: 5;
  margin-left: -70px;
  @include r(1250) {
    transform: translate(-50%);
    margin: 0 0; }
  @include r(820) {
    top: 10px; } }

.main-title {
  position: relative;
  max-width: 540px;
  margin-bottom: 61px;
  @include sfui;
  @include r(410) {
    margin-bottom: 20px; }
  &:after {
    position: absolute;
    bottom: -32px;
    left: 0;
    content: "";
    width: 72px;
    height: 4px;
    opacity: 0.4;
    background-color: #f7f7f7;
    @include r(410) {
      bottom: -12px; } } }

